<template>
  <div>
    <div class="containerBox">
      <!-- 页面标题 -->
      <div class="pageTopic">
        <span class="topicName">治疗记录</span>
        <el-button @click="back">返回</el-button>
      </div>
      <!-- 商品列表 -->
      <div class="list">
        <el-table :data="treatmentRecordListData" style="width: 100%" >
          <el-table-column prop="treatDate" label="治疗日期"></el-table-column>
          <el-table-column prop="treatTime" label="治疗时间"></el-table-column>
          <el-table-column prop="anode" label="阳极区"></el-table-column>
          <el-table-column prop="cathode" label="阴极区"></el-table-column>
          <el-table-column prop="current" label="电流(μA)"></el-table-column>
          <el-table-column prop="duration" label="时长(min)"></el-table-column>
        </el-table>
      </div>

      <!-- 分页 -->
      <div class="paging">
        <el-pagination @current-change="changePage"
                       :current-page="paginationData.page"
                       :hide-on-single-page="paginationData.size/paginationData.pageSize <= 1"
                       :page-size="paginationData.pageSize"
                       layout="total, prev, pager, next"
                       :total="paginationData.size">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import AppServerInf from "@/common/serverinf"

export default {
  data() {
    return {
      paginationData: {
        page: 1,
        pageSize: 10,
        size: 0
      },

      treatmentRecordListData: [],
    }
  },
  created() {
    const caseId = this.$route.params.caseId
    this.initRecordList(caseId)
  },
  methods: {
    initRecordList(caseId) {
      this.treatmentRecordListData = this.$db.treatmentRecordList(caseId)
    },
    changePage(val) {
      this.initRecordList(val)
    }
  }
}
</script>
<style>
</style>



